
import {Injectable} from "@angular/core";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {PUSTAK_SERVICE_PATHS} from "./pustak.service.consts";

@Injectable()
export class PustakService {

  constructor(private http:HttpClient) {

  }

  public async isUserAuthorized(jwtToken:any):Promise<boolean> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Authorization': 'Bearer ' + jwtToken
      })
    };
    try {
      let data:any = await this.makeAjaxCall(PUSTAK_SERVICE_PATHS.AUTHORIZATION, httpOptions);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }

  }

  private makeAjaxCall(url:string, payload:any):Promise<ArrayBuffer> {
    return this.http.get(url, payload).toPromise();
  }
}
