import {Component, OnInit} from "@angular/core";
import {BlobService} from "./services/BlobService";
import {GitHubService} from "./services/GitHubService";
import {Note} from "./models/Note";
import {MarkdownConverter} from "./converters/MarkdownConverter";
import {SharedMultiCastBus} from "../internal_services/SharedMultiCastBus";


@Component ({

  selector : 'reader',
  templateUrl : './reader.component.html',
  providers:[BlobService, GitHubService, MarkdownConverter]
})

export class ReaderComponent implements OnInit {

  note:Note = null;

  constructor(private blobService : BlobService, private mc:MarkdownConverter, private sharedBus:SharedMultiCastBus){}

    ngOnInit() {
      this.sharedBus.getRenderNoteRequest().subscribe(url=>{
        this.blobService.get(url)
          .subscribe((note:Note) => {
            this.note = note;
          });
      });
    }

}
