
import {Injectable} from "@angular/core";
import {Observable, Subject} from "rxjs/index";

@Injectable({ providedIn: 'root' })
export class SharedMultiCastBus {

  private readerChannel:Subject<any> = new Subject<any>();

  sendRenderNoteRequest(url:string) {
    this.readerChannel.next(url);
  }

  clearReaderMessage() {
    this.readerChannel.next();
  }

  getRenderNoteRequest():Observable<any> {
    return this.readerChannel.asObservable();
  }

}
