export class Note {

  private _id:string;
  private _author:string;
  private _version:number;
  private _content:string;

  constructor(){}

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get author(): string {
    return this._author;
  }

  set author(value: string) {
    this._author = value;
  }

  get version(): number {
    return this._version;
  }

  set version(value: number) {
    this._version = value;
  }

  public get content(): string {
    return this._content;
  }

  public set content(value: string) {
    this._content = value;
  }
}
